@import url(https://fonts.googleapis.com/css?family=Tomorrow&display=swap);
@import url(https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: 'Tomorrow', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-gradient(linear, right top, left top, from(rgb(199, 245, 41)) , to(rgb( 255, 184, 88)));
  background: linear-gradient(to left, rgb(199, 245, 41) 0% , rgb( 255, 184, 88) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
	font-family: 'Press Start 2P', cursive;
	font-size: 48px;
    text-shadow: 4px 4px 4px #aaa;
    color: #FF4F00;
}
